<template>
  <article>
    <h1><code>&lt;veui-radio&gt;</code></h1>
    <p>
      <veui-radio name="a" value="normal" v-model="a1"></veui-radio>
    </p>
    <p>
      <veui-radio checked></veui-radio>
    </p>
    <!-- <p>
      <veui-radio checked disabled></veui-radio>
    </p> -->
    <p>
      <veui-radio disabled v-model="a2"></veui-radio>
    </p>
    <p>
      <veui-radio disabled></veui-radio>
    </p>
  </article>
</template>

<script>
import { Radio } from 'veui'

export default {
  name: 'radio-demo',
  components: {
    'veui-radio': Radio
  },
  data () {
    return {
      a1: false,
      a2: true
    }
  }
}
</script>



